import { Loader } from "@googlemaps/js-api-loader";
import { MarkerClusterer, SuperClusterAlgorithm } from "@googlemaps/markerclusterer";
import Supercluster from 'supercluster';

const loader = new Loader({
    apiKey: "AIzaSyCMlg3335BMxPO51I7ZMpp-OXsTYCAaxYw",
    version: "weekly",
    libraries: ["places", "marker"]
  });
  
  loader.load().then(async () => {
    const map = new google.maps.Map(document.getElementById("map"), {
      center: { lat: 51.13531071, lng: 0.001977888 },
      zoom: 8,
      mapId: "8e6deb7a6acd250c"
    });
  
    try {
      const response = await fetch("https://jo435.brighton.domains/ci601/get-locations.php");
      if (!response.ok) {
        throw new Error(`Failed to fetch location data: ${response.statusText}`);
      }
      const locations = await response.json();
  
      const limitedLocations = locations.slice(0, 1000);
      console.log(`Number of locations received: ${limitedLocations.length}`);
  
      const markers = limitedLocations.map(location => {
        const position = { lat: parseFloat(location.Latitude), lng: parseFloat(location.Longitude) };
        console.log("Marker position:", position);
  
        const content = getInfoWindowContent(location);
        const infoWindow = new google.maps.InfoWindow({ content: content });
  
        const marker = new google.maps.Marker({
          position: position,
          map: map,
          title: location.Name
        });
  
        marker.addListener('click', () => {
          infoWindow.open(map, marker);
        });
  
        return marker;
      });
  
      console.log(`Number of markers created: ${markers.length}`);
  
      // Utilize the SuperClusterAlgorithm
      const clusterer = new MarkerClusterer({
        map: map,
        markers: markers,
        algorithm: new SuperClusterAlgorithm({
          maxZoom: 16,  // Example: Adjust based on your needs
          radius: 60   // Example: Adjust based on your needs
        })
      });
    } catch (error) {
      console.error(error);
    }
  });
  

function getInfoWindowContent(location) {
  let content = `
    <div>
      <h2>${location.Name.trim()}</h2>
      <p><strong>Lab No:</strong> ${location.LabNo.trim()}</p>
      <p><strong>Sex:</strong> ${location.Sex.trim()}</p>
      <p><strong>Age:</strong> ${location.Age.trim()}</p>
      <p><strong>Collected:</strong> ${location.Collected.trim()}</p>
      <p><strong>Received:</strong> ${location.Received.trim()}</p>
      <p><strong>Sample:</strong> ${location.Sample.trim()}</p>
      <p><strong>Isolate:</strong> ${location.Isolate.trim()}</p>
      <p><strong>Antibiotics:</strong></p><ul>`;
  for (let i = 1; i <= 18; i++) {
    const antibioticKey = `Antibiotic${i}`;
    if (location[antibioticKey].trim()) {
      content += `<li><strong>Antibiotic ${i}:</strong> ${location[antibioticKey].trim()}</li>`;
    }
  }
  content += "</ul></div>";
  return content;
}
